.previews{
    margin-top:2rem;
    &__links{
        

        &--top{
            display:flex;
            flex-direction: row;
            margin: 0 5%;
            
        }
        &--bottom{
            display:flex;
            flex-direction: row;
            margin: 0 5%;
        }
    }

    &__large{
        width:50%;
        border:1px solid $color-white;
        position: relative;
    }
    &__large img{
        width:100%;
        -webkit-filter: brightness(60%);
    }
    &__large img:hover{
        -webkit-filter: brightness(40%);
        -webkit-transition: all .4s ease;
        -moz-transition: all .4s ease;
        -o-transition: all .4s ease;
        -ms-transition: all .4s ease;
        transition: all .4s ease;
    }
    &__medium{
        width:33.33%;
        border:1px solid $color-white;
        position: relative;
    }
    &__medium img{
        width: 100%;
        -webkit-filter: brightness(60%);
    }
    &__medium img:hover{
        -webkit-filter: brightness(40%);
        -webkit-transition: all .4s ease;
        -moz-transition: all .4s ease;
        -o-transition: all .4s ease;
        -ms-transition: all .4s ease;
        transition: all .4s ease;
    }
    
    &__buttons{
        position: absolute;
        bottom:9rem;
        left:2rem;
    }
    &__title{
        position: absolute;
        bottom:3rem;
        left:2rem;
        text-transform: uppercase;
        color: $color-white;
        font-weight: 700;
        font-size: 3rem;
    }
}

main{
    display:flex;
    flex-direction: row;
    margin: 0 5%;
}

.news{
    width:68%;
    margin-right:1%;
    &__title{
        font-size:3rem;
        display: block;
        border-bottom: 3px solid $color-black;
    }
}
.topgames{
    width:30%;
    margin-left:1%;
    &__img img{
        width:100%;
    }
    &__title{
        font-size:3rem;
        display: block;
        border-bottom: 3px solid $color-black;
    }

}